// src/pages/usuarios/index.tsx
"use client";

import { useEffect, useState } from "react";
import { api } from "@/lib/api";
import UsuariosTable from "@/components/usuarios/UsuariosTable";
import UsuarioForm from "@/components/usuarios/UsuarioForm";
import { Button, Box, Typography, CircularProgress } from "@mui/material";

interface Usuario {
  id: number;
  correo: string;
  fecha_creacion: string;
}

export default function UsuariosPage() {
  const [usuarios, setUsuarios] = useState<Usuario[]>([]);
  const [loading, setLoading] = useState(true);
  const [showForm, setShowForm] = useState(false);

  useEffect(() => {
    cargarUsuarios();
  }, []);

  const cargarUsuarios = async () => {
    setLoading(true);
    try {
      const res = await api.get("/usuarios/ListarUsuario");
      setUsuarios(res.data);
    } catch (err) {
      console.error(err);
      alert("Error al cargar usuarios");
    } finally {
      setLoading(false);
    }
  };

  const handleCreate = async (correo: string, contrasena: string) => {
    try {
      await api.post("/usuarios", { correo, contrasena });
      alert("Usuario creado con éxito ✅");
      cargarUsuarios();
    } catch (err) {
      console.error(err);
      alert("Error al crear usuario ❌");
    }
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom>
        Usuarios
      </Typography>

      <Button variant="contained" onClick={() => setShowForm(true)} sx={{ mb: 2 }}>
        ➕ Nuevo Usuario
      </Button>

      <UsuarioForm open={showForm} onClose={() => setShowForm(false)} onCreate={handleCreate} />

      {loading ? (
        <CircularProgress />
      ) : (
        <UsuariosTable usuarios={usuarios} />
      )}
    </Box>
  );
}
